import React, { useState } from "react";
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
} from "reactstrap";

const Header = (props) => {
    const { data, currentPage } = props;
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => setIsOpen(!isOpen);
    console.log(`currentPage`, currentPage);
    return (
        <div>
            <Navbar
                color="light"
                light
                expand="md"
                className="navbar fixed-top bg-white navbar-expand-lg navbar-light maison-bold text-uppercase px-4 py-2"
            >
                <NavbarBrand href="/">
                    <img
                        className="header-logo-width"
                        src="../../images/gojek-logo.png"
                        alt="Gojek Logo"
                        title="Gojek"
                    />
                </NavbarBrand>
                <NavbarToggler onClick={toggle} />
                <Collapse isOpen={isOpen} navbar>
                    <Nav className="ml-auto" navbar>
                        {data.map((menuItem, key) => (
                            <div key={key}>
                                {menuItem.type === `externalLink` && (
                                    <NavItem
                                        key={key}
                                        className="mx-2 header-menu"
                                    >
                                        <NavLink
                                            href={menuItem.link}
                                            className="nav-link maison-bold"
                                        >
                                            {menuItem.name}

                                            <img
                                                width="18px"
                                                className="img-fluid pb-1"
                                                src="../../../../images/logo-gif.gif"
                                                alt="Gojek - Subscribe to newsletter"
                                            />
                                        </NavLink>
                                    </NavItem>
                                )}
                                {menuItem.type === `dropdown` ? (
                                    <UncontrolledDropdown
                                        nav
                                        inNavbar
                                        className="mx-2 header-menu nav-item"
                                    >
                                        <DropdownToggle
                                            nav
                                            caret
                                            className={
                                                `nav-link custom-nav-link maison-bold ` +
                                                (currentPage === `/articles` ||
                                                currentPage === `/tech-videos`
                                                    ? `active`
                                                    : ``)
                                            }
                                        >
                                            News &amp; Videos
                                        </DropdownToggle>
                                        <DropdownMenu
                                            right
                                            className="dropdown-menu custom-dropdown"
                                        >
                                            {menuItem.options.map(
                                                (option, key) => (
                                                    <DropdownItem
                                                        key={key}
                                                        className="dropdown-item navbar-dropdown-item text-muted"
                                                        tag="a"
                                                        href={option.link}
                                                    >
                                                        {option.name}
                                                    </DropdownItem>
                                                )
                                            )}
                                        </DropdownMenu>
                                    </UncontrolledDropdown>
                                ) : (
                                    (menuItem.type === `link` ||
                                        menuItem.type === `button`) && (
                                        <NavItem
                                            key={key}
                                            className={
                                                `mx-2 header-menu nav-item ${
                                                    data.type === `dropdown` &&
                                                    `dropdown`
                                                }` +
                                                (currentPage === menuItem.link
                                                    ? ` active`
                                                    : ``)
                                            }
                                        >
                                            <NavLink
                                                href={menuItem.link}
                                                className={
                                                    menuItem.type === `button`
                                                        ? `header-btn btn btn-success rounded-1 green-apply-btn text-white maison-bold`
                                                        : `nav-link maison-bold`
                                                }
                                            >
                                                {menuItem.name}
                                            </NavLink>
                                        </NavItem>
                                    )
                                )}
                            </div>
                        ))}
                    </Nav>
                </Collapse>
            </Navbar>
        </div>
    );
};

export default Header;
